import React, { useState } from 'react';
import SubNav from '../../components/common/layout/SubNav';
import CapTableDashboard from './components/CapTableDashboard';
import CapTableContent from './components/captable/CapTableContent';
import RoundHistory from './components/roundhistory/RoundHistory';
import ExitScenarios from './components/exitscenarios/ExitScenarios';
import InvestmentPlanning from './components/investment/InvestmentPlanning';

const CapTable = () => {
  const [activeSubTab, setActiveSubTab] = useState('dashboard');

  const subTabs = [
    { id: 'dashboard', name: 'Dashboard' },
    { id: 'captable', name: 'Cap Table' },
    { id: 'roundhistory', name: 'Round History' },
    { id: 'exitscenarios', name: 'Exit Scenarios' },
    { id: 'investment', name: 'Investment Planning' },
  ];

  const renderContent = () => {
    switch (activeSubTab) {
      case 'dashboard':
        return <CapTableDashboard />;
      case 'captable':
        return <CapTableContent />;
      case 'roundhistory':
        return <RoundHistory />;
      case 'exitscenarios':
        return <ExitScenarios />;
      case 'investment':
        return <InvestmentPlanning />;
      default:
        return <CapTableDashboard />;
    }
  };

  return (
    <div>
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Cap Table</h1>
      </div>
      <SubNav
        subTabs={subTabs}
        activeSubTab={activeSubTab}
        onSubTabChange={setActiveSubTab}
      />
      <div className="mt-8">
        {renderContent()}
      </div>
    </div>
  );
};

export default CapTable; 